import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

/**
 *
 * @param {*} type
 * @param {*} idVariabel
 * @returns
 */
export const setFormIndikatorAction = (formType, formValue) => {
  return { type: "ADD_INDIKATOR", formType, formValue };
};

/**
 * @param{idVariabel}
 * @return LISTOFIndikator
 */

export const GetIndikatorsAction = (type, idVariabel) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcIndikators}/GetIndikatorByIdVariabels?IdVariabel=${idVariabel}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type: "NAMA_VARIABEL", payload: data.data.variabel });
          dispatch({ type, payload: data.data.indikators });
        } else {
          Swal.fire("", `${data.returnMessage}  `, "warning");
        }
      });
  };
};

export const GetIndikatorByIdAction = (idIndikator) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcIndikators}/GetIndikator?IdIndikator=${idIndikator}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          console.log(data.data);
          dispatch(
            setFormIndikatorAction("IdIndikator", data.data.idIndikator)
          );
          dispatch(
            setFormIndikatorAction(
              "IdVariabel",
              data.data.idVariabel
            )
          );
          dispatch(setFormIndikatorAction("Nama", data.data.nama));
          dispatch(setFormIndikatorAction("Type", data.data.type));
          dispatch({ type: "MODAL_INDIKATOR", payload: true });
        } else {
          Swal.fire("", `${data.returnMessage}  `, "warning");
        }
      });
  };
};

/**
 * POST INDIKATOR
 */

export const PostIndikatorAction = (data) => {
  return (dispatch) => {
    loadingStatus(true);
    let idUser = sessionStorage.getItem("idUser");
    let idVariabel = data.IdVariabel;
    let param = `?IdUser=${idUser}&IdVariabel=${data.IdVariabel}&Nama=${data.Nama}&Type=${data.Type}`;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcIndikators}/AddIndikator${param}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;

        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_INDIKATOR", payload: false });
          dispatch({ type: "RESET_FORM_INDIKATOR" });
          dispatch(GetIndikatorsAction("GET_INDIKATORS", idVariabel));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

/**
 * Update
 */

export const UpdateIndikatorAction = (data) => {
  return (dispatch) => {
    loadingStatus(true);
    let idUser = sessionStorage.getItem("idUser");
    let idVariabel = data.IdVariabel;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcIndikators}/EditIndikator?IdUser=${idUser}`,
        data
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;

        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_INDIKATOR", payload: false });
          dispatch({ type: "RESET_FORM_INDIKATOR" });
          dispatch(GetIndikatorsAction("GET_INDIKATORS", idVariabel));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};


export const DeleteIndikator = (idIndikator, nama, idVariabel) => {
  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
    Swal.fire({
      title: "Apakah anda yakin?",
      text: `Akan menghapus Indikator ${nama}`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Ya, Akan Menghapus",
    }).then((result) => {
      if (result.isConfirmed) {
        http
          .post(
            `${process.env.REACT_APP_ROOT_API}${serviceName.svcIndikators}/DeleteIndikator?IdUser=${idUser}&IdIndikator=${idIndikator}`
          )
          .then((res) => {
            let result = res.data;
            if (result.isSuccess) {
              Swal.fire(`Sukses`, `Data ${nama} berhasil dihapus`, `success`);
              dispatch({ type: "RESET_FORM_INDIKATOR" });
              dispatch(GetIndikatorsAction("GET_INDIKATORS", idVariabel));
            } else {
              Swal.fire("Gagal", `${result.returnMessage}`, "error");
            }
          });
      }
    });
  };
};
