import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

export const setFormUserKecAction = (formType, formValue) => {
  console.log(formType)
  return { type: "ADD_USER_KEC", formType, formValue };
};

/**
 * @param {}
 * @result List of users
 * Description get api users
 */

export const GetUsersKecAPIAction = (type) => {
  return (dispatch) => {
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/GetUsers`
      )
      .then((res) => {
        let data = res.data;
        if (data.isSuccess) {
          let tmpArr = [];
          data.data.map((value, index) => {
            var roleNameTmp =
              " " +
              value.roles
                .map(function (i) {
                  return i.roleName;
                })
                .join(" || ");
            tmpArr.push({
              idUser: value.idUser,
              lastLogin: value.lastLogin,
              fullName: value.fullName,
              strStatus: value.strStatus,
              username: value.username,
              password: value.password,
              email: value.email,
              firstName: value.firstName,
              middleName: value.middleName,
              lastName: value.lastName,
              address: value.address,
              phoneNumber: value.phoneNumber,
              mobileNumber: value.mobileNumber,
              fileImage: value.fileImage,
              roleName: roleNameTmp,
            });
          });
          dispatch({ type, payload: tmpArr });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

export const GetUserAPIByIdUserAction = (idUser) => {
  return (dispatch) => {
    loadingStatus(true);
    dispatch({ type: "RESET_FORM_USER" });
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/GetUser?IdUser=${idUser}`
      )
      .then((result) => {
        loadingStatus(false);
        let data = result.data;
        if (data.isSuccess) {
          dispatch(setFormUserKecAction("Username", data.data.username));
          dispatch(setFormUserKecAction("IdUser", data.data.idUser));
          dispatch(setFormUserKecAction("Email", data.data.email));
          dispatch(setFormUserKecAction("FirstName", data.data.firstName));
          dispatch(setFormUserKecAction("MiddleName", data.data.middleName));
          dispatch(setFormUserKecAction("LastName", data.data.lastName));
          dispatch(setFormUserKecAction("Address", data.data.address));
          dispatch(setFormUserKecAction("PhoneNumber", data.data.phoneNumber));
          dispatch(setFormUserKecAction("MobileNumber", data.data.mobileNumber));
          var rolesTmpArr = [];
          data.data.roles.map((v, i) => {
            rolesTmpArr.push({
              value: v.idRole,
              label: v.roleName,
            });
          });

          dispatch(setFormUserKecAction("Roles", rolesTmpArr));
          dispatch({ type: "MODAL_USER", payload: true });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

export const AddUserAPIKecAction = (data) => {

  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {

    loadingStatus(true);
    let dataNewValue = { ...data };
    let tmpRoles = [];
    data.Roles.map((v) => {
      tmpRoles.push({
        IdRole: v.value,
      });
    });
    dataNewValue["Roles"] = tmpRoles;
    data = dataNewValue;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/AddPegawaiKecamatan?IdUser=${idUser}`,
        data
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_USER", payload: false });
          dispatch({ type: "RESET_FORM_USER" });
          dispatch(GetUsersKecAPIAction("GET_USER_LIST"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

export const SetUserInActiveKecAction = (idUserParam, fullName) => {
  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
    Swal.fire({
      title: "Apakah anda yakin?",
      text: `Akan Non aktifkan ${fullName}`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Ya, Akan Nonaktif",
    }).then((result) => {
      if (result.isConfirmed) {
        http
          .post(
            `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/SetUserInActive?IdUser=${idUser}&SetIdUser=${idUserParam}`
          )
          .then((res) => {
            let result = res.data;
            if (result.isSuccess) {
              Swal.fire(
                `Sukses`,
                `Data ${fullName} berhasil nonaktif`,
                `success`
              );
              dispatch({ type: "RESET_FORM_USER" });
              dispatch(GetUsersKecAPIAction("GET_USER_LIST"));
            } else {
              Swal.fire("Gagal", `${result.returnMessage}`, "error");
            }
          });
      }
    });
  };
};

