
import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

/**
 *
 * @param {*} type
 * @param {*} idVariabel
 * @returns
 */
export const setFormPertanyaanAction = (formType, formValue) => {
  return { type: "ADD_PERTANYAAN", formType, formValue };
};


export const GetPertanyaanAction = (type, idIndikator) => {
  return(dispatch) => {
      loadingStatus(true);
      http.get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterPertanyaan}/GetPertanyaansByIdIndikators?IdIndikator=${idIndikator}`
      ).then(res => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data.pertanyaans });
        } else {
          Swal.fire("", `${data.returnMessage}  `, "warning");
        }
      });
  }
}
